import React, {useEffect, useState, useCallback } from 'react';
import { shallowEqual, useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { Link, useLocation } from "react-router-dom";
import { 
  GET_HOME_DATA, 
  GET_SECTION_DATA,
  SET_STATE,
  SET_DISTRICT,
  SET_DATE,
  SET_SLOTS,
  SET_CENTER_INFO
} from './actions';
import './App.css';

function BlogHome(props) {
  const fetchQBlogs = () => async dispatch =>{
    let header = new Headers({
        "Access-Control-Allow-Origin":"*"
    });
    let sentData={
        method:"GET",
        mode: "cors",
        header: header
    };
    let request = {};
    let response = await fetch(`/`+props.match.params.slug.toLowerCase().split(' ').join('_')+`.json`, request);
    let data = await response.json();
    console.log(data); 
    dispatch({ type: GET_SECTION_DATA, payload: data })
  }

  const data = useSelector((state) => state.sectionData);
  console.log(data);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(fetchQBlogs());
    window.scrollTo(0, 0);
  }, []);
  const getTime = useCallback((dat) => {
    let d = new Date(dat).toLocaleString('en-US', { timeZone: 'Asia/Kolkata' });
    d = new Date(d);
    let ye = new Intl.DateTimeFormat('en', { year: 'numeric' }).format(d);
    let mo = new Intl.DateTimeFormat('en', { month: 'short' }).format(d);
    let da = new Intl.DateTimeFormat('en', { day: '2-digit' }).format(d);
    return mo+' '+da+', '+ye;
  });
  return (
    <React.Fragment>
      {/*props.match.params.slug.toLowerCase().split(' ').join('_');*/}
      <div className="" style={{marginTop: '5rem'}}>
        <div className="fcontainer" style={{padding: '50px 0px',textAlign: 'center', marginBottom: '30px', marginTop: '20px'}}>
            <h2  className="h2 text-uppercase" style={{margin: '30px 0px'}}>{props.match.params.slug.toUpperCase()}</h2>
                    <div style={{display: 'flex',justifyContent: 'center',alignItems: 'center'}}>
                      <span style={{background: '#ddd', borderRadius:'100%',width: '8px',
height: '8px',
position: 'relative',
display: 'inline-block', border: '1px solid #ccc'}}></span>
                      <span style={{background: '#ddd', borderRadius:'100%',width: '12px',
height: '12px',
position: 'relative',
display: 'inline-block', margin: '0px 6px', border: '1px solid #ccc'}}></span>
                      <span style={{background: '#ddd', borderRadius:'100%',width: '8px',
height: '8px',
position: 'relative',
display: 'inline-block', border: '1px solid #ccc'}}></span>
                    </div>

        </div>
      </div>
      <div className="">
        <div className="fcontainer" style={{paddingBottom: '50px'}}>
          <div className="flex-full-wrap ">
            {data.map((blog, key)=>{
                      return <Link key={key} className="btext flexhr1 mb-50 img-desc-wrp" target="_blank"  to={{pathname:blog.url}} style={{position: 'relative'}}>
                      <div className="desc-img-wrapper">
                        <img src={blog.image.toString()} className="desc-img" style={{width:'100%', height: '320px'}}/>
                      </div>
                        <div style={{textAlign: 'center', fontSize: '12px', color: '#666', marginTop: '12px', fontWeight: '500'}}>
                          <span>{getTime(blog.datetime)}</span>
                        </div>
                        <h4 style={{padding:'0px 16px', lineHeight:'1.2rem', fontSize:'14px', fontWeight:'600'}}>
                            {blog.heading}
                        </h4>
                        <div style={{padding:'0px 16px 16px 16px',fontSize: '12px', color: '#111', marginTop: '4px'}}>
                          <span>Source: {blog.source}</span>
                        </div>
                      
                      </Link>
            })
            }
          </div>
        </div>
      </div>


    </React.Fragment>
  );
}

export default BlogHome;
