function HamburgerIcon(){
  const onClick = useCallback((e, item) => {
    if(e.target.classList.contains('pencet-span')){
      if(!e.target.parentNode.classList.contains('Diam')){
        e.target.parentNode.classList.add('Diam');
        document.querySelector('.sidemenu').classList.add('active');
      }
      else{
        e.target.parentNode.classList.remove('Diam');
        document.querySelector('.sidemenu').classList.remove('active');
      }
    }
    else{
      if(!e.target.classList.contains('Diam')){
        e.target.classList.add('Diam');
        e.target.parentNode.querySelector('.sidemenu').classList.add('active');
      }
      else{
        e.target.classList.remove('Diam');
        e.target.parentNode.querySelector('.sidemenu').classList.remove('active');
      }
    }
  });
  const closeSideMenu = useCallback((e, item) => {
    document.querySelector('#pencet').classList.remove('Diam');
    document.querySelector('.sidemenu').classList.remove('active');
  });
  return(
    <div className="box  sidemenu-hamburger">
      <div id="pencet" className="cs" onClick={onClick}>
        <span className="pencet-span"></span>
        <span className="pencet-span"></span>
        <span className="pencet-span"></span>
      </div>
      <div className="sidemenu">
        <div onClick={closeSideMenu} style={{fontSize: '48px', marginBottom: '50px', cursor:'pointer'}}>&times;</div>

        <ul>
          <li>
            <Link to="/world" className="sidemenu-li-a">World</Link>
          </li>
          <li>
            <Link to="/business" className="sidemenu-li-a">Business</Link>
          </li>
          <li>
            <Link to="/technology" className="sidemenu-li-a">Technology</Link>
          </li>
          <li>
            <Link to="/entertainment" className="sidemenu-li-a">Entertainment</Link>
          </li>
          <li>
            <Link to="/sports" className="sidemenu-li-a">Sports</Link>
          </li>
          <li>
            <Link to="/science" className="sidemenu-li-a">Science</Link>
          </li>
          <li>
            <Link to="/health" className="sidemenu-li-a">Health</Link>
          </li>
        </ul>




      </div>
    </div>
  );
}


