import '../App.css';

import React, {useEffect, useState, useCallback} from 'react';


export const Checkbox = (props) => {

    const onClick = useCallback((e) => {
      const target = e.target.closest('.checkbox');
      if(target == null){
        if(!e.target.classList.contains('active')){
          e.target.classList.add('active');
        }
        else{
          e.target.classList.remove('active');
        }
      }
      else{
        if(!target.classList.contains('active')){
          target.classList.add('active');
        }
        else{
          target.classList.remove('active');
        }
      }
    });
  
    return (
      <span className="checkbox" onClick={onClick}>
        <svg className="check-ticked" style={{width: '10px', height: '10px', color:'#fff'}} >
            <path style={{fill: '#fff', margin: '50px'}} d="M6.039 1.43a1.11 1.11 0 0 1 1.517-.228c.483.342.588.998.234 1.466L4.431 7.1a1 1 0 0 1-1.492.115L.317 4.677a1.023 1.023 0 0 1 .002-1.483 1.113 1.113 0 0 1 1.535.002l1.641 1.59L6.04 1.428z"></path>
        </svg>
      </span>
    );
  }
  
  export const Radio = (props) => {
    const onClick = useCallback((e) => {
      const target = e.target.closest('.radio');
      if(target == null){
        if(!e.target.classList.contains('active')){
          e.target.classList.add('active');
        }
        else{
          e.target.classList.remove('active');
        }
      }
      else{
        if(!target.classList.contains('active')){
          target.classList.add('active');
        }
        else{
          target.classList.remove('active');
        }
      }
    });
    return (
      <div className="radio" onClick={onClick}>
        <span></span>
      </div>
    );
  }
  
  export const Input = (props) => {
    let classNames = props.hasOwnProperty('className') ? props.className+" "+"form-text" : "form-text";
    let placeholder = props.hasOwnProperty('placeholder') ? props.placeholder : "";
    let name = props.hasOwnProperty('name') ? props.name : "";
    return (
        <input className={classNames} type="text" placeholder={placeholder} name={name} />
    );
  }

  export const Textarea = (props) => {
    let classNames = props.hasOwnProperty('className') ? props.className+" "+"form-text" : "form-text";
    let placeholder = props.hasOwnProperty('placeholder') ? props.placeholder : "";
    let name = props.hasOwnProperty('name') ? props.name : "";
    return (
        <textarea className={classNames}  placeholder={placeholder} name={name}></textarea>
    );
  }

  export const Button = (props) => {
    let classNames = props.hasOwnProperty('className') ? props.className+" "+"form-button" : "form-button";
    let type = props.hasOwnProperty('type') ? props.type : "";
    return (
      <button type={type} className={classNames} onClick={props.onClick}>
        {props.text}
      </button>
    );
  }
  
  export const InputGroupButton = (props) => {
    let classNames = props.hasOwnProperty('className') ? props.className+" "+"form-text-group" : "form-text-group";
    let classNamesButton = props.hasOwnProperty('className') ? props.className+" "+"form-button-group" : "form-button-group";
    let placeholder = props.hasOwnProperty('placeholder') ? props.placeholder : "";
    let name = props.hasOwnProperty('name') ? props.name : "";
    let type = props.hasOwnProperty('type') ? props.type : "";
    return (
      <div className="input-group">
        <input className={classNames} type="text" placeholder={placeholder} name={name} onChange={props.setInputValue}/>
        <button type={type} className={classNamesButton} onClick={props.onClickButton}>
          {props.text}
        </button>
      </div>
    );
  }


