const NewsletterPopup = (props) => {
  const nl = localStorage.getItem('newsletter') !== null ? JSON.parse(localStorage.getItem('newsletter').toLowerCase()) : null;
  const closeModal = useCallback((e) => {
    localStorage.setItem('newsletter', false);
    document.getElementById('modal').classList.contains('active') === false ? document.getElementById('modal').classList.add('active') : document.getElementById('modal').classList.remove('active')
    document.querySelector('body').classList.remove('noscroll');
    props.toggleModal();
    props.props.subscribeNewsletter();
  });
  useEffect(() => {
    if(nl !== false){
      document.getElementById('modal').classList.contains('active') === false ? document.getElementById('modal').classList.add('active') : document.getElementById('modal').classList.remove('active')
      document.querySelector('body').classList.add('noscroll');
    }
  }, [nl]);
  return (
    <div>
      {nl !== false ?
      <div className="modal is-visible" id="modal">
        <div className="modal-overlay modal-toggle" onClick={closeModal}></div>
        <div className="modal-wrapper modal-transition">
          <div className="modal-body">
            <div className="">
              <div>
                <h2 className="text-center">Never miss a thing</h2>
                <h4 className="text-center">Subscribe to our newsletter!
                  <br />Always free • Unsubscribe anytime</h4>
                <div className="text-center flex justify-center">
                  <InputGroupButton   placeholder="E-Mail Address" text="Subscribe"/>
                </div>
                <div className="text-center flex justify-center ">
                  <button style={{background: 'transparent', border: '0px solid transparent', margin: '25px 0px'}} onClick={closeModal}>No Thanks</button>
                </div>

              </div>
              
            </div>

          </div>
          
          <div className="modal-footer flex-end">

          </div>

        </div>
      </div>
      :null}
    </div>
  );
}


