import { 
  GET_HOME_DATA, 
  GET_BLOG_DATA,
  SET_STATE,
  SET_DISTRICT,
  SET_DATE,
  SET_SLOTS,
  SET_CENTER_INFO,
  GET_SECTION_DATA,
  SET_COLOR
} from './actions';

const initialState = {
  homeData: {top:[], topics: []},
  sectionData: [],
  blogData: {},
  qDate: new Date(),
  qStateId: null,
  qDistrictId: null,
  centers: [],
  currentCenter: {sessions:[]},
  color: {value:'Select Value'}
};

function reducer(state = initialState, action) {
  switch(action.type) {  
    case GET_HOME_DATA:
      return {
        ...state,
        homeData: action.payload
      };
    case GET_SECTION_DATA:
      return {
        ...state,
        sectionData: action.payload
      };  
    case GET_BLOG_DATA:
      return {
        ...state,
        blogData: action.payload
      };  
    case SET_COLOR:
      return {
        ...state,
        color: action.payload
      };
    case SET_SLOTS:
      return {
        ...state,
        centers: action.payload
      };  
    case SET_DATE:
      return {
        ...state,
        qDate: action.payload
      };
    case SET_CENTER_INFO:
      return {
        ...state,
        currentCenter: action.payload
      };
    default:
      return state;
    }
  }
  export default reducer;
