import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import store from './store';
import './index.css';
import Layout from './Layout';
import App from './App';
import Contact from './Contact';
import BlogHome from './BlogHome';
import Blog from './Blog';
import reportWebVitals from './reportWebVitals';

import { BrowserRouter, Route, Switch, Link, useLocation, Redirect } from "react-router-dom";

window.onclick = function(event) {
  if(document.getElementsByClassName('in-select').length > 0){
    if (!event.target.closest('.in-select')) {
      Array.from(document.querySelectorAll('.in-select-options')).forEach((el) => el.classList.remove('show'));
    }
  }
  if(document.getElementsByClassName('sidemenu-hamburger').length > 0){
    if (!event.target.closest('.sidemenu-hamburger')) {
      document.querySelector('#pencet').classList.remove('Diam');
      document.querySelector('.sidemenu').classList.remove('active');
    }
  }
}
const site = window.location.hostname;//"myexamplesite.github.io"
ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      
      <BrowserRouter>
      { window.location.hostname == site ?
      <Layout >
        <Switch>
            
            <Route exact path='/' component={App} />
            <Route exact path='/contact' component={Contact} />
            <Route exact path="/:slug" component={BlogHome} />
            <Route exact path="/:slug/:title" component={Blog} />
            <Redirect path="*" to="/" />
            
          </Switch>
      </Layout>
      :null}
      </BrowserRouter>
      
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
